import { motion } from 'framer-motion';
import { FileText, Shield, Users, AlertTriangle, CheckCircle2, Scale } from 'lucide-react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Separator } from '@/components/ui/separator';
import { Badge } from '@/components/ui/badge';
import { cn } from '@/lib/utils';
import { typography } from '@/styles/design-system';

export default function TermosUsoPage() {
  const lastUpdated = '15 de Janeiro de 2025';

  const sections = [
    {
      id: 'aceitacao',
      icon: <CheckCircle2 className="h-5 w-5" />,
      title: '1. Aceitao dos Termos',
      content: [
        'Ao acessar e usar o Empresta a, voc concorda em cumprir estes Termos de Uso.',
        'Se voc no concordar com qualquer parte destes termos, no deve usar nossos servios.',
        'Reservamo-nos o direito de modificar ess termos a qualquer momento.',
        ' sua responsabilidade revisar periodicamente estes termos para estar ciente de quaisquer alteraes.',
      ],
    },
    {
      id: 'servicos',
      icon: <Users className="h-5 w-5" />,
      title: '2. Descrio dos Servios',
      content: [
        'O Empresta a  uma plataforma que conecta pessoas que desejam alugar itens com pessoas que possuem esses itens.',
        'Facilitamos a comunicao entre usurios, mas no somos parte das transaes entre eles.',
        'No garantimos a disponibilidade, qualidade ou condio dos itens listados.',
        'Os usurios so responsveis por suas prprias transaes e acordos.',
      ],
    },
    {
      id: 'cadastro',
      icon: <FileText className="h-5 w-5" />,
      title: '3. Cadastro e Conta de Usurio',
      content: [
        'Para usar nossos servios, voc deve criar uma conta fornecendo informaes precisas e atualizadas.',
        'Voc  responsvel por manter a confidencialidade de sua senha e por todas as atividades em sua conta.',
        'Deve notificar-nos imediatamente sobre qualquer uso no autorizado de sua conta.',
        'Reservamo-nos o direito de suspender ou encerrar contas que violem estes termos.',
      ],
    },
    {
      id: 'responsabilidades',
      icon: <Shield className="h-5 w-5" />,
      title: '4. Responsabilidades dos Usurios',
      content: [
        'Voc deve usar a plataforma de forma legal e respeitosa.',
        ' proibido publicar contedo ofensivo, ilegal ou que viole direitos de terceiros.',
        'Voc  responsvel pela veracidade das informaes que fornece sobre seus itens.',
        'Deve cumprir todos os acordos feitos com outros usurios atravs da plataforma.',
      ],
    },
    {
      id: 'transacoes',
      icon: <Scale className="h-5 w-5" />,
      title: '5. Transaes e Pagamentos',
      content: [
        'As transaes so realizadas diretamente entre os usurios.',
        'O Empresta a no  responsvel por disputas relacionadas a pagamentos ou qualidade dos itens.',
        'Recomendamos o uso de mtodos de pagamento seguros e a verificao dos itens antes do aluguel.',
        'Taxas de servio podem ser aplicadas conforme nossa poltica de preos.',
      ],
    },
    {
      id: 'limitacoes',
      icon: <AlertTriangle className="h-5 w-5" />,
      title: '6. Limitaes de Responsabilidade',
      content: [
        'O Empresta a no se responsabiliza por danos, perdas ou prejuzos decorrentes do uso da plataforma.',
        'No garantimos que o servio ser ininterrupto ou livre de erros.',
        'Nossa responsabilidade  limitada ao mximo permitido por lei.',
        'Voc usa nossos servios por sua prpria conta e risco.',
      ],
    },
  ];

  return (
    <div className="min-h-screen bg-gradient-to-b from-background to-muted/20 py-8">
      <div className="container mx-auto max-w-4xl px-4">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          className="mb-8 text-center"
        >
          <div className="mb-4 flex items-center justify-center">
            <div className="flex h-16 w-16 items-center justify-center rounded-full bg-primary/10">
              <FileText className="h-8 w-8 text-primary" />
            </div>
          </div>
          <h1 className={cn(typography.heading.h2, 'mb-4')}>Termos de Uso</h1>
          <p className={cn(typography.body.default, 'mx-auto max-w-2xl text-muted-foreground')}>
            Leia atentamente nossos termos de uso antes de utilizar a plataforma Empresta a.
          </p>
          <Badge variant="secondary" className="mt-4">
            ltima atualizao: {lastUpdated}
          </Badge>
        </motion.div>

        {/* Introduo */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.2 }}
          className="mb-8"
        >
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <AlertTriangle className="h-5 w-5 text-amber-500" />
                Importante
              </CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-muted-foreground">
                Estes Termos de Uso estabelecem as regras e condies para o uso da plataforma
                Empresta a. Ao criar uma conta ou usar nossos servios, voc concorda em seguir
                estas diretrizes. Se tiver dvidas, entre em contato conosco.
              </p>
            </CardContent>
          </Card>
        </motion.div>

        {/* Sees dos Termos */}
        <div className="space-y-6">
          {sections.map((section, index) => (
            <motion.div
              key={section.id}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6, delay: 0.1 * (index + 3) }}
            >
              <Card>
                <CardHeader>
                  <CardTitle className="flex items-center gap-2 text-primary">
                    {section.icon}
                    {section.title}
                  </CardTitle>
                </CardHeader>
                <CardContent>
                  <ul className="space-y-3">
                    {section.content.map((item, itemIndex) => (
                      <li key={itemIndex} className="flex items-start gap-3">
                        <div className="mt-2 h-1.5 w-1.5 flex-shrink-0 rounded-full bg-primary/60" />
                        <p className="leading-relaxed text-muted-foreground">{item}</p>
                      </li>
                    ))}
                  </ul>
                </CardContent>
              </Card>
            </motion.div>
          ))}
        </div>

        {/* Contato e Suporte */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.8 }}
          className="mt-12"
        >
          <Card className="border-primary/20 bg-primary/5">
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Users className="h-5 w-5 text-primary" />
                Dvidas sobre os Termos?
              </CardTitle>
            </CardHeader>
            <CardContent>
              <p className="mb-4 text-muted-foreground">
                Se voc tiver alguma dvida sobre estes Termos de Uso ou precisar de
                esclarecimentos, no hesite em entrar em contato conosco.
              </p>
              <div className="flex flex-col gap-2 sm:flex-row sm:items-center sm:gap-4">
                <div className="flex items-center gap-2 text-sm">
                  <div className="h-2 w-2 rounded-full bg-primary" />
                  <span className="font-medium">Email:</span>
                  <span className="text-primary">contato@emprestae.com.br</span>
                </div>
                <div className="flex items-center gap-2 text-sm">
                  <div className="h-2 w-2 rounded-full bg-primary" />
                  <span className="font-medium">Telefone:</span>
                  <span className="text-primary">(11) 9999-9999</span>
                </div>
              </div>
            </CardContent>
          </Card>
        </motion.div>

        {/* Rodap Legal */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 1.0 }}
          className="mt-8"
        >
          <Separator className="mb-6" />
          <div className="text-center">
            <p className="text-sm text-muted-foreground">
               2025 Empresta a. Todos os direitos reservados.
            </p>
            <p className="mt-2 text-xs text-muted-foreground">
              Estes termos so regidos pelas leis brasileiras e qualquer disputa ser resolvida nos
              tribunais competentes do Brasil.
            </p>
          </div>
        </motion.div>
      </div>
    </div>
  );
}
