import { AppHeader } from '@/components/ui/app-header';
import PageContainer from '@/components/ui/page-container';
import { useAuth } from '@/hooks/useAuth';
import { useNavigate } from 'react-router-dom';

export default function PoliticaPrivacidade() {
  const navigate = useNavigate();
  const { user } = useAuth();
  return (
    <>
      <AppHeader showBackButton onBack={() => navigate(-1)} title="Poltica de Privacidade" />
      <PageContainer className="mx-auto max-w-2xl">
        <section className="rounded-xl bg-white/90 p-6 shadow-lg ring-1 ring-gray-200">
          <h1 className="mb-4 text-2xl font-bold text-gray-900 sm:text-3xl">
            Poltica de Privacidade
          </h1>
          <p className="mb-6 text-base text-muted-foreground">
            Esta Poltica de Privacidade explica de forma clara e objetiva como coletamos, usamos e
            protegemos seus dados pessoais no Empresta a, em conformidade com a LGPD (Lei Geral de
            Proteo de Dados - Lei 13.709/2018).
          </p>
          <ul className="mb-6 space-y-4 text-base text-foreground">
            <li>
              <strong>1. Coleta de Dados:</strong> Coletamos apenas os dados necessrios para
              cadastro, uso do app e segurana, como nome, e-mail, localizao e informaes dos
              itens.
            </li>
            <li>
              <strong>2. Uso dos Dados:</strong> Utilizamos seus dados para viabilizar emprstimos,
              facilitar contato entre usurios, melhorar a experincia e garantir a segurana da
              plataforma.
            </li>
            <li>
              <strong>3. Compartilhamento:</strong> No vendemos dados. Compartilhamos apenas o
              necessrio para funcionamento do app, como exibir informaes de contato entre
              usurios interessados em transaes.
            </li>
            <li>
              <strong>4. Direitos do Titular:</strong> Voc pode acessar, corrigir ou excluir seus
              dados a qualquer momento, bem como solicitar informaes sobre o tratamento dos seus
              dados.
            </li>
            <li>
              <strong>5. Segurana:</strong> Adotamos medidas tcnicas e administrativas para
              proteger seus dados contra acessos no autorizados, vazamentos ou usos indevidos.
            </li>
            <li>
              <strong>6. Cookies:</strong> Utilizamos cookies apenas para melhorar a navegao e
              funcionalidades essenciais do app.
            </li>
            <li>
              <strong>7. Reteno:</strong> Os dados so mantidos apenas pelo tempo necessrio para
              cumprir as finalidades do app e obrigaes legais.
            </li>
            <li>
              <strong>8. Contato:</strong> Em caso de dvidas sobre privacidade ou para exercer seus
              direitos, entre em contato pelo e-mail suporte@emprestaae.com.
            </li>
          </ul>
          <p className="text-xs text-muted-foreground">
            ltima atualizao: {new Date().toLocaleDateString('pt-BR')}
          </p>
        </section>
      </PageContainer>
    </>
  );
}
