/**
 * Funo simples de hash para gerar nmeros consistentes baseados em string
 */
function simpleHash(str: string): number {
  let hash = 0;
  for (let i = 0; i < str.length; i++) {
    const char = str.charCodeAt(i);
    hash = (hash << 5) - hash + char;
    hash = hash & hash; // Convert to 32bit integer
  }
  return Math.abs(hash);
}

/**
 * Gera um bairro consistente baseado no ID do item
 */
export function getNeighborhoodForItem(itemId: string): string {
  const hash = simpleHash(itemId);
  const index = hash % SAO_PAULO_NEIGHBORHOODS.length;
  return SAO_PAULO_NEIGHBORHOODS[index];
}

/**
 * Gera uma distncia consistente baseada no ID do item
 * Retorna distncias entre 0.5km e 15km
 */
export function getDistanceForItem(itemId: string): number {
  const hash = simpleHash(itemId + '_distance');

  // Gera um nmero entre 0 e 1
  const normalized = (hash % 1000) / 1000;

  // Distribui as distncias de forma realstica
  if (normalized < 0.4) {
    // 40% chance de distncia entre 0.5km e 3km
    return Math.round(((normalized * 2.5) / 0.4 + 0.5) * 10) / 10;
  } else if (normalized < 0.7) {
    // 30% chance de distncia entre 3km e 7km
    return Math.round((((normalized - 0.4) * 4) / 0.3 + 3) * 10) / 10;
  } else {
    // 30% chance de distncia entre 7km e 15km
    return Math.round((((normalized - 0.7) * 8) / 0.3 + 7) * 10) / 10;
  }
}

/**
 * Formata a distncia para exibio
 */
export function formatDistance(distance: number): string {
  if (distance < 1) {
    return `${Math.round(distance * 1000)}m`;
  }
  return `${distance.toFixed(1)}km`;
}

/**
 * Gera informaes de localizao consistentes baseadas no ID do item
 */
export function generateMockLocationInfo(itemId: string) {
  return {
    neighborhood: getNeighborhoodForItem(itemId),
    distance: getDistanceForItem(itemId),
  };
}

/**
 * @deprecated Use generateMockLocationInfo(itemId) instead
 * Mantido para compatibilidade, mas gera dados aleatrios
 */
export function getRandomNeighborhood(): string {
  const randomIndex = Math.floor(Math.random() * SAO_PAULO_NEIGHBORHOODS.length);
  return SAO_PAULO_NEIGHBORHOODS[randomIndex];
}

/**
 * @deprecated Use generateMockLocationInfo(itemId) instead
 * Mantido para compatibilidade, mas gera dados aleatrios
 */
export function getRandomDistance(): number {
  const random = Math.random();

  if (random < 0.4) {
    return Math.round((Math.random() * 2.5 + 0.5) * 10) / 10;
  } else if (random < 0.7) {
    return Math.round((Math.random() * 4 + 3) * 10) / 10;
  } else {
    return Math.round((Math.random() * 8 + 7) * 10) / 10;
  }
}
