import {
  Award,
  HelpCircle,
  MessageCircle,
  Minus,
  Package,
  Plus,
  Search,
  Settings,
  Shield,
  User,
  Zap,
} from 'lucide-react';
import React, { useState } from 'react';

import { Button } from '@/components/ui/button';
import { cn } from '@/lib/utils';

interface FAQItem {
  id: string;
  question: string;
  answer: string;
  category:
    | 'primeiros-passos'
    | 'busca'
    | 'processo'
    | 'seguranca'
    | 'pagamento'
    | 'perfil'
    | 'itens'
    | 'suporte'
    | 'tecnico';
  priority: number;
  hasCTA?: boolean;
  ctaText?: string;
  ctaAction?: () => void;
}

interface FAQSectionProps {
  className?: string;
  onContactClick?: () => void;
  onChatClick?: () => void;
}

const FAQSection: React.FC<FAQSectionProps> = ({ className, onContactClick, onChatClick }) => {
  // Permitir apenas um item aberto por vez
  const [openItem, setOpenItem] = useState<string | null>(null);
  const [selectedCategory, setSelectedCategory] = useState<string>('all');

  const faqData: FAQItem[] = [
    // PRIMEIROS PASSOS
    {
      id: 'faq-1',
      question: 'Como fao para me cadastrar na plataforma?',
      answer:
        'Baixe o app, clique em "Registrar", preencha seus dados pessoais e faa a verificao com CPF, RG e comprovante de residncia. O processo leva cerca de 24 horas para aprovao.',
      category: 'primeiros-passos',
      priority: 1,
    },
    {
      id: 'faq-2',
      question: 'Quais documentos preciso para verificar minha conta?',
      answer:
        'Voc precisa de CPF, RG (ou CNH) e comprovante de residncia atualizado (at 3 meses). Todos os documentos devem estar legveis e sem rasuras.',
      category: 'primeiros-passos',
      priority: 1,
    },
    {
      id: 'faq-3',
      question: 'Como funciona o processo completo de emprstimo?',
      answer:
        'Busque o item desejado, solicite o emprstimo, aguarde aprovao do proprietrio, combine local e horrio de retirada pelo chat, retire o item, use conforme acordado e devolva no prazo. Ambos avaliam a experincia ao final.',
      category: 'primeiros-passos',
      priority: 1,
    },
    {
      id: 'faq-4',
      question: 'Posso usar a plataforma sem verificar minha identidade?',
      answer:
        'No. A verificao de identidade  obrigatria para garantir a segurana de todos os usurios. Contas no verificadas tm acesso limitado apenas para navegao.',
      category: 'primeiros-passos',
      priority: 2,
    },

    // BUSCA E LOCALIZAO
    {
      id: 'faq-5',
      question: 'Como funciona a busca por localizao?',
      answer:
        'O app usa sua localizao atual para mostrar itens prximos. Voc pode ajustar o raio de busca de 1km at 50km e filtrar por bairro ou regio especfica.',
      category: 'busca',
      priority: 1,
    },
    {
      id: 'faq-6',
      question: 'Posso alterar minha localizao para ver itens de outras regies?',
      answer:
        'Sim! No filtro de busca, voc pode digitar qualquer endereo ou CEP para explorar itens em outras cidades. til para planejar emprstimos quando viajar.',
      category: 'busca',
      priority: 2,
    },
    {
      id: 'faq-7',
      question: 'Como so calculadas as distncias dos itens?',
      answer:
        'As distncias so calculadas em linha reta entre sua localizao e o item. O tempo estimado de deslocamento considera trnsito mdio da regio.',
      category: 'busca',
      priority: 3,
    },

    // PROCESSO DE EMPRSTIMO
    {
      id: 'faq-8',
      question: 'Como combino a entrega/retirada do item?',
      answer:
        'Aps aprovao, use o chat integrado para combinar local, horrio e detalhes. Recomendamos locais pblicos e seguros. Alguns proprietrios oferecem entrega por taxa adicional.',
      category: 'processo',
      priority: 1,
    },
    {
      id: 'faq-9',
      question: 'Posso estender o prazo de um emprstimo?',
      answer:
        'Sim, se o proprietrio concordar. Solicite a extenso pelo chat antes do vencimento. O valor adicional ser calculado proporcionalmente e cobrado automaticamente.',
      category: 'processo',
      priority: 1,
    },
    {
      id: 'faq-10',
      question: 'O que acontece se eu atrasar a devoluo?',
      answer:
        'H uma tolerncia de 2 horas. Aps isso,  cobrada multa de 20% do valor dirio. Atrasos frequentes podem resultar em suspenso da conta.',
      category: 'processo',
      priority: 2,
    },
    {
      id: 'faq-11',
      question: 'Quanto tempo leva para aprovar minha solicitao?',
      answer:
        'A maioria das solicitaes  respondida em at 2 horas. Se no houver resposta em 24h, voc pode cancelar automaticamente sem custos.',
      category: 'processo',
      priority: 2,
    },
    {
      id: 'faq-12',
      question: 'Posso cancelar um emprstimo aps a reserva?',
      answer:
        'Sim. At 24h antes da retirada: cancelamento gratuito. Entre 24h e 2h antes: taxa de 10%. Menos de 2h antes: taxa de 30% do valor total.',
      category: 'processo',
      priority: 3,
    },

    // SEGURANA
    {
      id: 'faq-13',
      question: ' seguro emprestar itens com pessoas desconhecidas?',
      answer:
        'Sim! Todos os usurios so verificados com documentos, temos sistema de avaliaes, seguro contra danos e suporte 24/7. Sempre prefira locais pblicos para trocas.',
      category: 'seguranca',
      priority: 1,
    },
    {
      id: 'faq-14',
      question: 'O que acontece se o item for danificado durante o emprstimo?',
      answer:
        'Nosso seguro cobre danos acidentais at R$ 5.000. Comunique imediatamente pelo app, tire fotos e abra um chamado. A anlise leva at 48h.',
      category: 'seguranca',
      priority: 1,
    },
    {
      id: 'faq-15',
      question: 'Como funciona o sistema de cauo/garantia?',
      answer:
        'Para itens acima de R$ 200,  retido um valor de cauo no carto (no cobrado). A cauo  liberada automaticamente aps devoluo sem problemas.',
      category: 'seguranca',
      priority: 2,
    },
    {
      id: 'faq-16',
      question: 'O que fazer se um item no foi devolvido?',
      answer:
        'Comunique imediatamente pelo app. Temos 48h para mediar o conflito. Se no resolvido, acionamos a cauo e, se necessrio, medidas legais.',
      category: 'seguranca',
      priority: 2,
    },

    // PAGAMENTO
    {
      id: 'faq-17',
      question: 'Quais so as taxas e como funciona o pagamento?',
      answer:
        'Voc paga o valor do aluguel + taxa de servio de 8%. Aceitamos carto, PIX e carteira digital. O pagamento  processado apenas aps confirmao da retirada.',
      category: 'pagamento',
      priority: 1,
    },
    {
      id: 'faq-18',
      question: 'Como recebo o pagamento pelos meus emprstimos?',
      answer:
        'O pagamento  liberado 24h aps a devoluo confirmada. Voc recebe 92% do valor (descontamos 8% de taxa). Transferncia via PIX ou conta bancria.',
      category: 'pagamento',
      priority: 1,
    },
    {
      id: 'faq-19',
      question: 'Posso negociar preos diretamente com o usurio?',
      answer:
        'No. Todos os pagamentos devem ser feitos pela plataforma para garantir segurana e cobertura do seguro. Negociaes externas violam nossos termos.',
      category: 'pagamento',
      priority: 2,
    },
    {
      id: 'faq-20',
      question: 'Como funciona o reembolso em caso de problemas?',
      answer:
        'Se o item no estiver conforme anunciado ou houver problemas graves, voc pode solicitar reembolso total em at 2h aps a retirada. Anlise em at 24h.',
      category: 'pagamento',
      priority: 3,
    },

    // PERFIL E AVALIAES
    {
      id: 'faq-21',
      question: 'Como funciona o sistema de avaliaes?',
      answer:
        'Aps cada emprstimo, ambos avaliam a experincia (1-5 estrelas) e podem deixar comentrios. Avaliaes so pblicas e influenciam na reputao do usurio.',
      category: 'perfil',
      priority: 1,
    },
    {
      id: 'faq-22',
      question: 'Como posso melhorar minha avaliao na plataforma?',
      answer:
        'Seja pontual, cuide bem dos itens, comunique-se claramente, cumpra acordos e trate outros usurios com respeito. Usurios bem avaliados tm prioridade nas solicitaes.',
      category: 'perfil',
      priority: 2,
    },
    {
      id: 'faq-23',
      question: 'Como edito as informaes do meu perfil?',
      answer:
        'V em "Meu Perfil" > "Editar Perfil". Voc pode alterar foto, descrio e preferncias. Dados pessoais (nome, CPF) s podem ser alterados via suporte.',
      category: 'perfil',
      priority: 2,
    },
    {
      id: 'faq-24',
      question: 'Posso bloquear usurios especficos?',
      answer:
        'Sim. No perfil do usurio, clique nos trs pontos e selecione "Bloquear". Usurios bloqueados no podem ver seus itens nem solicitar emprstimos.',
      category: 'perfil',
      priority: 3,
    },

    // ITENS E CATEGORIAS
    {
      id: 'faq-25',
      question: 'Como cadastro um item para emprstimo?',
      answer:
        'Clique em "+" no menu principal, tire fotos ntidas, preencha ttulo, descrio, categoria, preo e condies. Itens so aprovados em at 24h aps anlise.',
      category: 'itens',
      priority: 1,
    },
    {
      id: 'faq-26',
      question: 'Que tipos de itens posso encontrar na plataforma?',
      answer:
        'Ferramentas, eletrnicos, equipamentos esportivos, instrumentos musicais, itens de festa, roupas especiais, livros, jogos e muito mais. Veja nossa lista de itens proibidos.',
      category: 'itens',
      priority: 1,
    },
    {
      id: 'faq-27',
      question: 'Posso definir regras especficas para meus itens?',
      answer:
        'Sim! Voc pode definir horrios de retirada, locais preferenciais, instrues de uso, requisitos mnimos de avaliao do locatrio e outras condies especiais.',
      category: 'itens',
      priority: 2,
    },
    {
      id: 'faq-28',
      question: 'Como posso confiar na qualidade dos itens?',
      answer:
        'Todos os itens tm fotos, descries detalhadas, avaliaes de outros usurios e histrico de emprstimos. Proprietrios com boa reputao so destacados.',
      category: 'itens',
      priority: 2,
    },
    {
      id: 'faq-29',
      question: 'Existe limite de itens que posso cadastrar?',
      answer:
        'Usurios novos podem cadastrar at 5 itens. Com boa reputao, o limite aumenta para 20 itens. Usurios premium tm limite ilimitado.',
      category: 'itens',
      priority: 3,
    },

    // SUPORTE E PROBLEMAS
    {
      id: 'faq-30',
      question: 'Como posso entrar em contato com o suporte?',
      answer:
        'Suporte 24/7 disponvel pelo chat do app, WhatsApp (11) 9999-9999, e-mail contato@empresta-ae.com.br ou telefone. Tempo mdio de resposta: 15 minutos.',
      category: 'suporte',
      priority: 1,
    },
    {
      id: 'faq-31',
      question: 'Como reporto um problema com um usurio?',
      answer:
        'No perfil do usurio ou chat, clique em "Reportar". Descreva o problema detalhadamente. Nossa equipe analisa em at 24h e toma as medidas necessrias.',
      category: 'suporte',
      priority: 1,
    },
    {
      id: 'faq-32',
      question: 'Como contesto uma avaliao injusta?',
      answer:
        'V em "Meu Perfil" > "Avaliaes" > "Contestar". Explique sua verso dos fatos com evidncias. Nossa equipe analisa e pode remover avaliaes inadequadas.',
      category: 'suporte',
      priority: 2,
    },
    {
      id: 'faq-33',
      question: 'Posso cancelar minha conta? Como?',
      answer:
        'Sim. V em "Configuraes" > "Conta" > "Excluir Conta". Certifique-se de no ter emprstimos pendentes. O processo  irreversvel aps confirmao.',
      category: 'suporte',
      priority: 3,
    },

    // TCNICO
    {
      id: 'faq-34',
      question: 'O app funciona offline?',
      answer:
        'Parcialmente. Voc pode ver itens salvos e conversas recentes offline, mas precisa de internet para buscar novos itens, fazer solicitaes e enviar mensagens.',
      category: 'tecnico',
      priority: 2,
    },
    {
      id: 'faq-35',
      question: 'Como ativo as notificaes?',
      answer:
        'V em "Configuraes" > "Notificaes" e ative as que desejar: novas mensagens, solicitaes, lembretes de devoluo, ofertas especiais, etc.',
      category: 'tecnico',
      priority: 2,
    },
    {
      id: 'faq-36',
      question: 'Posso usar a plataforma pelo navegador?',
      answer:
        'Atualmente apenas pelo app mvel (iOS e Android). Estamos desenvolvendo a verso web que estar disponvel em breve.',
      category: 'tecnico',
      priority: 3,
    },
  ];

  // Filtro de categoria
  const categories = [
    { key: 'all', label: 'Todas', icon: <HelpCircle className="h-4 w-4" /> },
    { key: 'primeiros-passos', label: 'Primeiros Passos', icon: <Zap className="h-4 w-4" /> },
    { key: 'busca', label: 'Busca', icon: <Search className="h-4 w-4" /> },
    { key: 'processo', label: 'Emprstimos', icon: <Package className="h-4 w-4" /> },
    { key: 'seguranca', label: 'Segurana', icon: <Shield className="h-4 w-4" /> },
    { key: 'pagamento', label: 'Pagamento', icon: <Award className="h-4 w-4" /> },
    { key: 'perfil', label: 'Perfil', icon: <User className="h-4 w-4" /> },
    { key: 'itens', label: 'Itens', icon: <Package className="h-4 w-4" /> },
    { key: 'suporte', label: 'Suporte', icon: <MessageCircle className="h-4 w-4" /> },
    { key: 'tecnico', label: 'Tcnico', icon: <Settings className="h-4 w-4" /> },
  ];

  // Filtrar FAQ por categoria, depois ordenar por prioridade
  const filteredFAQ = faqData
    .filter((item) => {
      const matchesCategory = selectedCategory === 'all' || item.category === selectedCategory;
      return matchesCategory;
    })
    .sort((a, b) => a.priority - b.priority);

  const getCategoryIcon = (category: string) => {
    switch (category) {
      case 'primeiros-passos':
        return <Zap className="h-5 w-5 text-primary" />;
      case 'busca':
        return <Search className="h-5 w-5 text-primary" />;
      case 'processo':
        return <Package className="h-5 w-5 text-primary" />;
      case 'seguranca':
        return <Shield className="h-5 w-5 text-primary" />;
      case 'pagamento':
        return <Award className="h-5 w-5 text-primary" />;
      case 'perfil':
        return <User className="h-5 w-5 text-primary" />;
      case 'itens':
        return <Package className="h-5 w-5 text-primary" />;
      case 'suporte':
        return <MessageCircle className="h-5 w-5 text-primary" />;
      case 'tecnico':
        return <Settings className="h-5 w-5 text-primary" />;
      default:
        return <HelpCircle className="h-5 w-5 text-primary" />;
    }
  };

  return (
    <div className={cn('space-y-10', className)}>
      {/* Header do FAQ */}
      <div className="text-center">
        <p className="fluid-body text-muted-foreground">
          Encontre respostas para suas dvidas sobre cadastro, emprstimos, pagamentos e muito mais.
        </p>
      </div>

      {/* Filtros de categoria modernos */}
      <div className="flex flex-wrap justify-center gap-3">
        {categories.map((cat) => (
          <Button
            key={cat.key}
            variant={selectedCategory === cat.key ? 'default' : 'outline'}
            size="sm"
            onClick={() => setSelectedCategory(cat.key)}
            className={cn(
              'flex items-center gap-2 rounded-full px-4 py-2 transition-all duration-300 hover:scale-105',
              selectedCategory === cat.key
                ? 'bg-primary text-primary-foreground shadow-lg shadow-primary/25'
                : 'border-muted-foreground/20 bg-background/50 hover:bg-muted/50'
            )}
          >
            {cat.icon}
            {cat.label}
          </Button>
        ))}
      </div>

      {/* Lista de FAQs modernizada */}
      <div className="mx-auto max-w-4xl space-y-4">
        {filteredFAQ.length === 0 ? (
          <div className="rounded-3xl border border-muted/50 bg-muted/20 p-12 text-center">
            <div className="mx-auto max-w-sm space-y-4">
              <div className="mx-auto flex h-16 w-16 items-center justify-center rounded-full bg-muted">
                <HelpCircle className="h-8 w-8 text-muted-foreground" />
              </div>
              <h3 className="fluid-subtitle font-semibold">Nenhuma pergunta encontrada</h3>
              <p className="text-muted-foreground">
                Tente ajustar sua busca ou selecionar uma categoria diferente.
              </p>
            </div>
          </div>
        ) : (
          filteredFAQ.map((item, index) => (
            <div
              key={item.id}
              className={cn(
                'group overflow-hidden rounded-3xl border-2 transition-all duration-500 hover:shadow-lg',
                openItem === item.id
                  ? 'border-primary/40 bg-gradient-to-br from-primary/5 to-primary/10 shadow-xl shadow-primary/10'
                  : 'border-primary/20 bg-background/80 backdrop-blur-sm hover:border-primary/30 hover:bg-background hover:shadow-primary/5'
              )}
            >
              <button
                className="w-full p-6 text-left transition-all duration-300 focus:outline-none focus:ring-2 focus:ring-primary/20 focus:ring-offset-2"
                onClick={() => setOpenItem(openItem === item.id ? null : item.id)}
                aria-expanded={openItem === item.id}
                aria-controls={`faq-content-${item.id}`}
              >
                <div className="flex items-center justify-between gap-6">
                  <div className="flex flex-1 items-center gap-4">
                    <div className="flex-shrink-0">
                      <div
                        className={cn(
                          'flex h-12 w-12 items-center justify-center rounded-2xl transition-all duration-300',
                          openItem === item.id
                            ? 'bg-primary/20 text-primary shadow-lg shadow-primary/20'
                            : 'bg-muted/50 text-muted-foreground group-hover:bg-primary/10 group-hover:text-primary'
                        )}
                      >
                        {getCategoryIcon(item.category)}
                      </div>
                    </div>
                    <h3 className="fluid-subtitle font-semibold text-foreground transition-colors duration-300 group-hover:text-primary">
                      {item.question}
                    </h3>
                  </div>
                  <div className="flex-shrink-0">
                    <div
                      className={cn(
                        'flex h-10 w-10 items-center justify-center rounded-full transition-all duration-500',
                        openItem === item.id
                          ? 'rotate-180 bg-primary/20 text-primary'
                          : 'bg-muted/50 text-muted-foreground group-hover:bg-primary/10 group-hover:text-primary'
                      )}
                    >
                      {openItem === item.id ? (
                        <Minus className="h-5 w-5" />
                      ) : (
                        <Plus className="h-5 w-5" />
                      )}
                    </div>
                  </div>
                </div>
              </button>

              <div
                className={cn(
                  'overflow-hidden transition-all duration-500 ease-in-out',
                  openItem === item.id ? 'max-h-96 opacity-100' : 'max-h-0 opacity-0'
                )}
              >
                <div className="border-t border-primary/10 px-6 pb-6 pt-4">
                  <div className="ml-16 animate-fade-in">
                    <p className="fluid-body leading-relaxed text-muted-foreground">
                      {item.answer}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          ))
        )}
      </div>
    </div>
  );
};

export default FAQSection;
